#sec-tab-wrap-fix{
	position: absolute;
	width: 100%;
	z-index: 200;
	height: .47rem;
}
.sec-tab-wrap-fix{
	z-index: -1;
	opacity: 0;
}
.sec-tab-wrap{
	position: relative;
	background-color: #fff;

	.sec-tab{
		padding: .08rem 0;
		background-color: #f3f4f6;

		li{
			/*width: 1.39rem;*/
			height: .47rem;
			font-size: .25rem;
			text-align: center;
			line-height: .47rem;
			background-color: #ffffff;
			border-right: 2px solid #f3f4f6;
			-webkit-box-sizing:border-box;
			box-sizing:border-box;
			padding: 0 .44rem;
			width: auto;

			a{
				color: #1c1c1c;
				display: block;
				height: 100%;
				width: 100%;
				text-align: center;
				line-height: .47rem;
				white-space: nowrap;
			}

			&.active{
				background-color: #474747;

				a{
					color: #fff;
				}
			}
		}
	}
}