.count-down{
	color: #5c5c5c;
	position: relative;
	padding: .26rem 0;
	text-align: center;

	h3{
		font-size: .17rem;
		color: #858688;
		text-align: center;
		padding-bottom: .1rem;
	}

	.timer{
		font-size: 0;
		padding: 0 .6rem;
	}

	.timer-inner{
		position: relative;
		height: .24rem;
	}

	.bdrx1{
		position: absolute;
		width: 100%;
		left: 0;
		top: 50%;
		-webkit-transform: translateY(-50%);
		transform: translateY(-50%);

		&::after{
			height: 1px;
			content: '';
			width: 100%;
			border-bottom-width: 1px;
			border-bottom-style: solid;
			border-color: #c0c0c0;
			position: absolute;
			bottom: 0;
			left: 0;
			-ms-transform: scaleY(.5);
			-webkit-transform: scaleY(.5);
			transform: scaleY(.5);
			z-index: 10;
		}
	}

	span{
		padding: 0 .26rem;
		background-color: #f3f4f6;
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
		z-index: 100;
		font-size: .26rem;
		white-space: nowrap;
	}

	em{
		font-weight: normal;
		font-size: .24rem;
	}

	label{
		font-size: .18rem;
	}

	&.waring .font-icon, &.waring em{
		color: #ffdb14;
	}

	.icon-tick-black{
		width: 0.2rem;
		height: 0.2rem;
		vertical-align: middle;
	}
}