.red-operate{
	position: relative;
	display: -ms-box;
	display: -webkit-box;
	display: box;
	background: #fff;

	a{
		display: block;
		-ms-box-flex: 1;
		-webkit-box-flex: 1;
		box-flex: 1;
		font-size: 0.27rem;
		text-align: center;
		padding: 0.16rem 0;
		color: #000;

		&:last-child p{
			border-right: none;
		}

		&.active{
			color: #f9384e;

			.icon-filter{
				background-position: -0.4rem -3.09rem;
			}

			.icon-pass{
				background-position: -0.41rem -2.73rem;
			}
		}

		&.sort-up{
			color: #f9384e;

			.icon-tris::before{
				border-color: transparent transparent #f9384e transparent;
			}
		}

		&.sort-down{
			color: #f9384e;

			.icon-tris::after{
				border-color: #f9384e transparent transparent transparent;
			}
		}
	}

	p{
		position: relative;
		/*border-right: 1px solid #d6d6d6;*/
		line-height: 0.26rem;
	}

	span{
		display: inline-block;
		vertical-align: middle;
	}

	.has-goods-btn span{
		padding-right: 0.3rem;
		position: relative;
	}

	.icon-pass{
		width: 0.2rem;
		height: 0.16rem;
		position: absolute;
		right: 0;
		top: 50%;
		-webkit-transform: translateY(-50%);
		transform: translateY(-50%);
	}

	.icon-tris{
		width: 0.16rem;
		height: 0.2rem;
		vertical-align: middle;
		padding-left: 0.12rem;
	}

	.icon-filter{
		width: 0.2rem;
		height: 0.26rem;
		margin-left: 0.12rem;
		vertical-align: middle;
	}
}