.red-coupon{
	padding: .16rem .1rem;

	.coupon-row{
		display: -ms-box;
		display: -webkit-box;
		display: box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		height: .9rem;

		.coupon-col{
			display: block;
			-ms-box-flex: 1;
			-webkit-box-flex: 1;
			box-flex: 1;
			-ms-flex: 1;
			-webkit-flex: 1;
			flex: 1;
			height: 100%;
			-webkit-box-sizing:border-box;
			box-sizing:border-box;
			background: url(../images/coupon_bg.jpg) repeat-x;
			background-size: auto 100%;
			position: relative;

			.coupon-type{
				font-size: .15rem;
				font-weight: lighter;
				color: #af4340;
				margin-left: .13rem;
			}

			&.timeout{
				background: url(../images/coupon_bg_dis.jpg) repeat-x;
				background-size: auto 100%;

				.coupon-right{
					background: url(../images/coupon_right_dis.jpg) no-repeat right top;
					background-size: auto 100%;
				}

				.coupon-left{
					background: url(../images/coupon_left_dis.jpg) no-repeat left top;
					background-size: auto 100%;
				}

				.right-con{
					background: url(../images/coupon_line_dis.jpg) no-repeat left center;
					background-size: auto .74rem;
					color: #a8a8a8;
				}

				.coupon-type{
					color: #fff;
				}
			}

		}

		.normal{
			background: url(../images/coupon_bg1.jpg) repeat-x;
			background-size: auto 100%;

			.coupon-left{
				background: url(../images/coupon_normal_left.jpg) no-repeat left top;
				background-size: auto 100%;
			}

			.coupon-right{
				background: url(../images/coupon_normal_right.jpg) no-repeat right top;
				background-size: auto 100%;
			}
		}

		.baitiao{
			background: url(../images/coupon_baitiao_bg.jpg) repeat-x;
			background-size: auto 100%;

			.coupon-left{
				background: url(../images/coupon_baitiao_left.jpg) no-repeat left top;
				background-size: auto 100%;
			}

			.coupon-right{
				background: url(../images/coupon_baitiao_right.jpg) no-repeat right top;
				background-size: auto 100%;
			}

			.right-con{
				background: url(../images/coupon_bt_line.jpg) no-repeat left center;
				background-size: auto .74rem;
			}

			.coupon-type{
				color: #ab8532;
			}
		}

		.coupon-left{
			width: .54rem;
			height: 100%;
			background: url(../images/coupon_left1.jpg) no-repeat left top;
			background-size: auto 100%;
			position: absolute;
			left: 0;
			top: 0;
		}

		.left-con{
			position: absolute;
			left: .4rem;
			left: 0;
			top: 50%;
			-webkit-transform: translateY(-50%);
			transform: translateY(-50%);
			font-size: 0;
			width: 2.36rem;
			text-align: center;
		}

		.coupon-con{
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: 100;
			line-height: 1;
		}

		.coupon-right{
			width: .11rem;
			height: 100%;
			background: url(../images/coupon_right.jpg) no-repeat right top;
			background-size: auto 100%;
			position: absolute;
			right: 0rem;
			top: 0;
		}

		.type{
			font-size: .25rem;
			color: #fff;
		}

		.cost{
			font-size: .52rem;
			color: #fff;
			padding-left: .1rem;
		}

		.rule{
			display: inline-block;
			padding-left: .1rem;
		}

		.name{
			font-size: .42rem;
			color: #fff;
			padding-bottom: .05rem;
			font-weight: 700;

			span{
				font-size: .24rem;
			    font-weight: lighter;
			    padding-right: .09rem;
			}
		}

		&.one-col{
			.left-con{
				width: 5.3rem;
			}
		}

		&.two-col{
			.left-con{
				/*left: .34rem;*/
			}

			.right-con{
				width: .63rem;
			}

			.coupon-col:first-child{
				margin-right: 0.04rem;
			}

			.cost{
				padding-left: .04rem;
			}
		}

		.rule-detail{
			font-size: .18rem;
			color: #fff;
		}

		.right-con{
			position: absolute;
			height: 100%;
			width: .8rem;
			right: .1rem;
			top: 0;
			background: url(../images/coupon_line.jpg) no-repeat left center;
			background-size: auto .74rem;
			font-size: .22rem;
			color: #fff;
		}

		.inner{
			position: absolute;
			left: 50%;
			top: 50%;
			-webkit-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			white-space: nowrap;
		}
	}
}